@import url(https://fonts.googleapis.com/css2?family=Inter:wght@200;400;600&display=swap);
* {
  padding: 0;
  margin: 0;
  text-decoration: none;
  text-transform: none;
  list-style: none;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', sans-serif;
  background: #E7F6FF;
}

.all_content {
  background: #FFFFFF;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
  border-radius: 20px;
  position: relative;
  top: 85px;
}

.container {
  width: 100%;
  padding: 0 .75rem;
  margin: 0 auto;
}

.currency {
  margin-left: 5px;
}

.btn {
  display: inline-flex;
  align-content: center;
  justify-content: center;
  align-items: center;
  background: #FFFFFF;
  color: #000;
  border: 1px solid #F8F8F8;
  border-radius: 7px;
  padding: 10px;
  cursor: pointer;
}

.btn_main {
  background: #9DD558;
  border-radius: 18px;
  font-weight: 600;
  font-size: 16px;
  text-align: center;
  color: #FFFFFF;
  padding: 17px;
}

.go_to, .back_to {
  position: relative;
}

.go_to:before, .back_to:before {
  content: '';
  position: absolute;
  transition: transform .2s ease-in-out;
  top: 1px;
  width: 16px;
  height: 14px;
  background-image: url('data:image/svg+xml,<svg width="16" height="14" viewBox="0 0 16 14" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M14.7144 7L1.00007 7" stroke="white" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/><path d="M7 13L1 7L7 1" stroke="white" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/></svg>');
}

.go_to:before {
  right: -30px;
  transform: rotate(180deg);
}

.back_to:before {
  left: -30px;
}

button:hover > .go_to:before {
  transform: rotate(180deg) translateX(-10px);
}

button:hover > .back_to:before {
  transform: translateX(-10px);
}

@media (max-width: 767px) {
  .all_content {
    top: 0;
  }
}

@media (max-width: 575px) {
  .all_content {
    border-radius: 0;
  }

  .btn {
    padding: 6px;
  }

  .btn_main {
    padding: 13px;
    font-size: 14px;
    border-radius: 15px;
  }
}

@media (max-width: 375px) {
  .btn {
    padding: 10px;
  }
}

@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}

@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}

@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}

@media (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
}

@media (min-width: 1400px) {
  .container {
    max-width: 1320px;
  }
}
